import {NetworkOnly} from 'workbox-strategies';
import {Plugin} from 'workbox-background-sync';
import idb from "./libs/idb-promised";

function showNotification () {
  self.registration.showNotification('Background sync success!', {
    body: '```'
  });
};

  export default function backgroundSyncStrategy(queueName, storeName, key ) {
    const dbPromise = idb.open('response-cache', 1, function(upgradeDb) {
      if (!upgradeDb.objectStoreNames.contains('responses')) {
        const responses = upgradeDb.createObjectStore('responses', {keyPath: 'id', autoIncrement: true});
        responses.createIndex('store', 'store');
      }
      if (!upgradeDb.objectStoreNames.contains('items')) {
        const items = upgradeDb.createObjectStore('items', {keyPath: ['__meta_store','__meta_id']});
        items.createIndex('__meta_store', '__meta_store');
      }
    });

    let getKey = (item) => item[key];
    if(Array.isArray(key)){
      getKey = (item) => key.reduce( (k, keyAttr) => (k.push(item[keyAttr]),k), [])
    }

    let getKeyStore = (item) => [storeName, getKey(item)];

    let isDummy = (item) => String(item[key]).startsWith('dummy_');
    if(Array.isArray(key)) {
      isDummy = (item) => key.reduce( (b, key) => (b && String(item[key]).startsWith('dummy_')), true);
    }

    const updateItems = async (responses) => {
      await dbPromise.then( async (db) => {
        let t = db.transaction('items', 'readwrite');
        let store = t.objectStore('items');
        let res;
        for(res of responses) {
          if(res.response.ok) {
            if(isDummy(res.data)) {
              store.delete(getKeyStore(res.data));
            }
            await store.put({...res.responseData, __meta_id: getKey(res.responseData), __meta_store: storeName});
          } else {
            let item = await store.get(getKeyStore(res.data));
            if(item) {
              await store.put({...item, __meta_pending: true, __meta_error: res.responseData});
            }
          }
        }
      })
    }

    const itemsOnSync = async (queue) => {
      let syncedResponces = [];
      let entry;
      while(entry = await queue.queue.shiftRequest()) {
        try{
          let data = await entry.request.clone().json();
          let response = await fetch(entry.request.clone());
          syncedResponces.push({data, response, responseData: await response.clone().json() });
          // showNotification();
        }catch(error) {
          await queue.queue.unshiftRequest(entry);
          await updateItems(syncedResponces);
          throw error;
        }
      }
      await updateItems(syncedResponces);
    }


    const itemBgSyncPlugin = new Plugin(
      queueName,
      {
        onSync: itemsOnSync
      }
    );

    const backgroundSyncStrategy = new NetworkOnly({
      plugins: [itemBgSyncPlugin],
    });

    return backgroundSyncStrategy;
  }
