/* eslint-disable no-undef */
import {registerRoute, NavigationRoute} from 'workbox-routing';
import {precacheAndRoute, createHandlerBoundToURL} from 'workbox-precaching';
import {StaleWhileRevalidate, NetworkOnly} from 'workbox-strategies';
import backgroundSyncStrategy from './background-sync-strategy';
import idb from "./libs/idb-promised";

precacheAndRoute(self.__WB_MANIFEST);

console.log("hello from custom-sw.js");

console.log(`Yay! Workbox is loaded `);


const handler = createHandlerBoundToURL('index.html');
const navigationRoute = new NavigationRoute(handler, {
	denylist: [new RegExp('^(\/api).*$'), new RegExp('\.(?:js|css|png|jpeg|jpg|json)$')]
});
registerRoute(navigationRoute);

registerRoute(
  /\.(?:js|css)$/,
  new StaleWhileRevalidate({
      cacheName: 'static-resources',
  })
);

self.addEventListener('push', (event) => {
  const title = 'Get Started With Workbox';
  const options = {
    body: event.data.text()
  };
  event.waitUntil(self.registration.showNotification(title, options));
});

const showNotification = (req) => {
  self.registration.showNotification('Background sync success!', {
    body: '```'
  });
};

let skySchoolDB = idb.open('skyschool', 1);

function backgroundSyncRouteFactory(routes, queueName, storeName, key='id') {
  const strategy = backgroundSyncStrategy(queueName, storeName, key);
  for(let route of routes) {
    registerRoute(route.path, strategy, route.method);
  }
}

backgroundSyncRouteFactory([{path: /\/api\/students(\/*)$/, method: 'POST'}, {path: /\/api\/students\/\w+(\/*)$/, method: 'PUT'}], 'students-queue', 'students', 'id');
backgroundSyncRouteFactory([{path: /\/api\/reports(\/*)$/, method: 'POST'}, {path: /\/api\/reports\/\w+(\/*)$/, method: 'PUT'}], 'reports-queue', 'reports', 'id');
backgroundSyncRouteFactory([{path: /\/api\/examination\/records(\/*)$/, method: 'POST'}], 'records-queue', 'records', ['exam_id', 'student_id']);
backgroundSyncRouteFactory([{path: /\/api\/teachers(\/*)$/, method: 'POST'}, {path: /\/api\/teachers\/\w+(\/*)$/, method: 'PUT'}], 'teachers-queue', 'teachers', 'id');
backgroundSyncRouteFactory([{path: /\/api\/teachers\/duty(\/*)$/, method: 'POST'}, {path: /\/api\/teachers\/duty\/\w+(\/*)$/, method: 'PUT'}], 'teacher-duties-queue', 'teacher-duties', 'id');
backgroundSyncRouteFactory([{path: /\/api\/teachers\/assessment(\/*)$/, method: 'POST'}, {path: /\/api\/teachers\/assessment\/\w+(\/*)$/, method: 'PUT'}], 'teacher-assessments-queue', 'teacher-assessments', 'id');
backgroundSyncRouteFactory([{path: /\/api\/staffs(\/*)$/, method: 'POST'}, {path: /\/api\/staffs\/\w+(\/*)$/, method: 'PUT'}], 'staffs-queue', 'staffs', 'id');
backgroundSyncRouteFactory([{path: /\/api\/staffs\/offdays(\/*)$/, method: 'POST'}, {path: /\/api\/staffs\/offdays\/\w+(\/*)$/, method: 'PUT'}], 'staff-offdays-queue', 'staff-offdays', 'id');
